.flex_item {
  max-width: 100%;
  &.title {
    font-size: 1.3em;
    font-weight: bold;
    text-align: center;
    justify-content: center;
    max-width: 30%;
  }

  &.item {
    max-width: 30%;

    &_title {
      vertical-align: middle;
      text-transform: uppercase;
      margin-left: 50px;

      &_withDesc {
        margin-bottom: 0;
      }
    }
    &_description {
      margin-top: 0;
      font-weight: 500;
      margin-left: 50px;
    }
  }
}
